.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
body {
  margin: 0;
  padding: 0;
}
.board {
  width: 500px;
  height: 500px;
  background-color: aquamarine ;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
}
.board .row {
  display: flex;
  flex: 33%;
  flex-direction: row;
}
.square {
  flex: 33%;
  height: 100%;
  border: 1px solid black;
  cursor: pointer;
  display: grid;
  place-items: center;
  font-size: 30px;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}
.square:hover {
  background-color: lightblue;
}